body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hidden {
  display: none; }

.App {
  flex: 1 1; }

.NavBar {
  margin-bottom: 24px; }
  .NavBar .nav-title {
    cursor: pointer;
    border-right: solid 1px white;
    padding-right: 8px;
    margin-right: 8px; }
  .NavBar .Toolbar {
    flex-grow: 1; }
    .NavBar .Toolbar .btn-tool {
      margin: 0px 8px; }

.TileMap {
  overflow: scroll;
  border: 30px solid #eee;
  max-height: 75vh;
  background-color: #eee; }

.ToolPanel {
  background-color: #eee; }
  .ToolPanel .tool-pane {
    padding: 0px 30px 30px 30px; }

.TileForm .form, .Settings .form {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column; }
  .TileForm .form input, .Settings .form input {
    border-bottom: none;
    padding: 0px 12px; }
  .TileForm .form .text-field, .Settings .form .text-field {
    margin: 8px 0px; }
  .TileForm .form .inline-group, .Settings .form .inline-group {
    flex-direction: row;
    justify-content: center; }
  .TileForm .form .grid-field, .Settings .form .grid-field {
    max-width: 100px;
    margin-top: 12px;
    margin-right: 20px; }

.TilePalette .btn-full-width {
  width: 100%; }

.TilePlate {
  flex: 1 1;
  justify-content: flex-end;
  flex-direction: row;
  display: flex; }

.JsonView {
  padding: 0px 74px; }

.TilePreview {
  display: inline;
  background-color: #333;
  font-size: 24px;
  padding: 10px;
  border-radius: 6px; }

.konvajs-content {
  background-color: #333; }

